
.header {
  display: flex;
  align-items: center;
 justify-content: center;
  text-align: right;
}

header li {
  font-family: var(--font-primary);
  font-size: 3rem;  
  color: var(--beige);
}

/* effects */

.text-flicker-in-glow:hover {
  transform: scale(1.1);
	-webkit-animation: text-flicker-in-glow 1s linear both;
	        animation: text-flicker-in-glow 1s linear both;
}
/* ----------------------------------------------
 * animation text-flicker-in-glow
 */
